import React, { useState, useEffect } from "react";

import { AiOutlineDoubleLeft, AiOutlineDoubleRight } from "react-icons/ai";
// import "./newSidebar.scss";
import NewPositionForm from "./NewPosition/NewPositionForm";
import AddNewEvent from "./NewEvent/AddNewEvent.js";
import AddNewProject  from "./AddNewProject.js";
import UserPermissions from "./UserPermissions.js";

function NewSideBar({addProjToggle, setAddProjToggle}) {
  const [open, setOpen] = useState(true);

  const linkes = [
    " ",
    " ",
    " ",
    "  ",
    "  ",
    "  ",
  ];
  const [tab, setTab] = useState("");

  const [toggleUserPermissions, setToggleUserPermissions] = useState(false);
  const [toggleLinks, setToggleLinks] = useState(false)

  useEffect(() => {
    if(addProjToggle){
      setToggleLinks(true)
    }
    if(!toggleLinks) {
      setAddProjToggle((p)=> !p)
    }
  }, [])

  const handelsideBar = () => {
    setOpen(!open);
    setTab("");
    
  };

 
  return (
    <div className='main-sidebar'>
    <div className="sideBar">
      <div className="sideBar-container">
        {/* <div >  */}

        {open ? (
          <div className="sidbar-left-arrow">
            <AiOutlineDoubleLeft onClick={handelsideBar} />
          </div>
        ) : (
          <div className="sidebar-admin-right-arrow">
            <div className='menu-toggle'>
            <AiOutlineDoubleRight onClick={handelsideBar} />
            <label className="arrow-icon"> </label>
            </div>
            <ul className="sidebarList">
              {linkes.map((link, index) => {
                return (
                  <li
                    key={index}
                    className="row"
                    id={window.location.pathname === link ? 'active' : ""}
                    onClick={() => {
                      setTab(link);
                      setToggleLinks(!toggleLinks)
                    }}
                  >
                    {link}
                  </li>
                );
              })}
            </ul>
          </div>
        )}
        {/* </div> */}

        <div>{tab === " " && <NewPositionForm toggleLinks={toggleLinks} setToggleLinks={setToggleLinks}/>}</div>
        <div>{tab === " " && <AddNewEvent  toggleLinks={toggleLinks} setToggleLinks={setToggleLinks}/>}</div>
        <div>{tab === " " && <AddNewProject toggleLinks={toggleLinks} setToggleLinks={setToggleLinks} />}</div>
        <div >
          {tab === "  " && <UserPermissions toggleLinks={toggleLinks} setToggleLinks={setToggleLinks} />}</div>
      </div>
    </div>
    </div>
  );
}

export default NewSideBar;
