import React from 'react';
import {Link} from "react-router-dom";
import ArrowBackIcon from "@mui/icons-material/ArrowBack";
import JobCard from "./sub_cmps/JobCard";

const Jobs = () => {
    return (
        <div className="jobs">
            <span className="jobs-title">#_ '</span>
            <div className="jobs-sub-title-container">
                <span className="jobs-sub-title">   </span>
                <span className="jobs-sub-title-link">
                    <Link to="/">
                        <span className="jobs-link"> </span>
                        <ArrowBackIcon className="sub-title-icon"/>
                    </Link>
                </span>
            </div>
            <div className="jobs-cards">
                <JobCard/>
                <JobCard/>
                <JobCard/>
            </div>
        </div>
    );
};

export default Jobs;