import React, { useState } from 'react';

export const Subscribe = () => {
    const [mail, setmail] = useState('')

    const handlChange = (ev) => {
        setmail(ev.target.value)
    }
    const sendMail = () => {
        console.log('mail sended');
    }
    return (
        <div className="subscribe flex column">
            <h1 className="stay-update">#_</h1>
            <span className="stay-update-sub">    ,    </span>
            <br />
            <div className="input-and-btn">
                <input type="email" className="subscribe-input" name="mail"
                    value={mail} onChange={handlChange} placeholder=" -" />
                <button className="btn-send-subscribe" onClick={sendMail}> !</button>
            </div>
        </div>
    );
};