const initialState = {
  jobs: [
    {
      _id: "a1a1a",
      text: "/   ?      ! ( 32391)",
      job: " ",
      loc: "  ",
    },
    {
      _id: "a2a2a",
      text: `/ /
             Java
                  `,
      job: " ",
      loc: "",
    },
    {
      _id: "a3a3a",
      text: `   
            Developer Fe Junior
                 
            Computer Fitness and Vision`,
      job: " ",
      loc: " ",
    },
  ],
};

export function jobReducer(state = initialState, action) {
  switch (action.type) {
    case "SET_JOBS":
      return { ...state, jobs: action.jobs };
    case "REMOVE_JOB":
      return {
        ...state,
        jobs: state.jobs.filter((job) => job._id !== action.jobId),
      };
    case "ADD_JOB":
      return { ...state, jobs: [action.job, ...state.jobs] };
    case "UPDATE_JOB":
      return {
        ...state,
        jobs: state.jobs.map((job) => {
          return job._id === action.job._id ? action.job : job;
        }),
      };
    default:
      return state;
  }
}
